<template>
  <div v-loading='loading'>
    <cms-breadcrumb to="/role" type='list' @click="rest">
      <li>{{$route.name}}</li>
    </cms-breadcrumb>
    <div class="cms-form-layout">
      <cms-prompt :collapse='false'>
        <li>可进行添加修改角色管理员，维护管理员权限</li>
      </cms-prompt>
      <el-form ref="form" :rules="rules" :model="formData" label-width="210px">
           <el-form-item label="角色名" prop="roleName" :error='errorMessage.roleName'>
              <el-input v-model="formData.roleName" :autofocus="true" ref="first"></el-input>
          </el-form-item>
          <el-form-item label="描述" prop="remark" :error='errorMessage.remark'>
              <el-input v-model="formData.remark" type="textarea" :autosize="{ minRows: 3}"></el-input>
          </el-form-item>
         <el-form-item ref="rule" label="权限设置" prop="menuid" :error='errorMessage.menuid'>
            <role-tree ref='role'  :menuIds='formData.menuid'  v-if="roleStatus"></role-tree>
          </el-form-item>
      </el-form>
       <div class="cms-form-footer">
        <cms-button  @click="update" v-color >保存修改</cms-button>
      </div>
    </div>
  </div>
</template>
<script>
import roleTree from './roleTree'
import rules from '@/rules/index.js'
import form from '@/mixins/form.js'
import { coreRoleGet, coreRoleUpdate } from '@/api/admin/role.js'
var defaultData = {
  'roleName': '',
  'remark': '',
  'menuid': []
}
export default {
  mixins: [form],
  components: {
    roleTree
  },
  data() {
    return {
      roleStatus: true, // 权限树手动重载
      formData: Object.assign({}, defaultData),
      rules: {
        roleName: [rules.required('请填写角色名'), rules.len(1, 10)],
        remark: [rules.required('请填写角色描述'), rules.max(140, '描述最大长度为140个字符')],
        menuid: [rules.required('至少选择一个权限', 'change')]
      }
    }
  },
  methods: {
    getFormData(id) {
      coreRoleGet({ id: id }).then(res => {
        this.formData = res.data
        this.formData.menuid = res.data.menus.map(v => v.id)
        // this.roleStatus = false
        // setTimeout(() => {
        //   this.roleStatus = true
        // }, 1)
      })
    },
    update() {
      this.formData.menuid = this.$refs.role.getPermsInfo().map(v => v.id)// 获取到权限树
      if (this.formData.menuid.length !== 0) {
        this.$refs.rule.clearValidate() // 每次修改之前先清掉权限的规则
        this.updateForm(coreRoleUpdate, this.formData, '/role', 'list')
      } else {
        this.$errorMessage('至少选择一个权限')
      }
    },
    reset() {
      this.getFormData(this.$route.query.id)
      this.roleStatus = false
      setTimeout(() => {
        this.roleStatus = true
      }, 1)
    }
  },
  created() {
    this.getFormData(this.$route.query.id)
  }
}
</script>

<style>
</style>



